@tailwind base;
@tailwind components;
@tailwind utilities;

$themeTransition: all 200ms ease-in;

@font-face {
  font-family: "ktn";
  src: url('../public/fonts/kill-the-noise.otf');
}

// * {
//   border: 1px solid firebrick;
// }

@media (max-height: 900px) {
  .sm-height {
    height: 140%;
  }
}

.bghome1, .bghome2, .bghome3, .bgabout1 {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  
}

.bghome1 {
  background-image: url('/images/diazmcgregor.png');
}

.bghome2 {
  background-image: url('/images/jonesgustafsson.png');
}

.bghome3 {
  background-image: url('/images/rodtangjohnson.png');
}

.bgabout1 {
  background-image: url('/images/crowd.png');
}

.defaultTransition {
  transition: $themeTransition;
}

.brand {
  font-family: ktn;
}

[class='dark'] {
  transition: $themeTransition;
  color: #e3e3e3;
  background-color: #101010;
}

[class='light'] {
  transition: $themeTransition;
  color: #101010;
  background-color: #e3e3e3;
}

.text-shadow {
  text-shadow: 0px 0px 5px black;
}

.loader-animation {
  @keyframes colorFade {
    0% {
      background-color: #1a1a1a;
    }
    50% {
      background-color: #262626;
    }
    100% {
      background-color: #1a1a1a;
    }
  }
  animation: colorFade 1s ease-in infinite;

}

.toggle-switch {
  width: 50px;
  height: 20px;
  border-radius: 8px;
  background-color: #ccc;
  position: relative;
  cursor: pointer;
  transition: background-color 0.3s;
}

.toggle-switch.on {
  background-color: #b72f3f;
}

.slider {
  width: 23px;
  height: 18px;
  border-radius: 35%;
  background-color: white;
  position: absolute;
  top: 1px;
  left: 1px;
  transition: left 0.3s;
}

.toggle-switch.on .slider {
  left: 26px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.spinner {
  border-width: 8px;
  border-style: solid;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

[class='light'] .spinner {
  border-color: #cccccc;
  border-top-color: #b72f3f;
}

[class='dark'] .spinner {
  border-color: #333;
  border-top-color: #b72f3f;
}